import React, {Component} from 'react';
import '../../assets/css/Stocks.css';
import CompanyInfo from './CompanyInfo';
import axios from 'axios';
import {buildUrl, replacePathVar, TICKER_PAGE, PRICE_COMPOSITE} from '../../config/UrlConfig';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import ListSubHeader from '@material-ui/core/ListSubheader';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Drawer from '@material-ui/core/Drawer';
import TextField from '@material-ui/core/TextField';

class StockList extends Component {
    constructor() {
        super();
        this.state = {
            tickerList : [
                {'tickerSymbol': '',
                 'companyName': 'Loading...'
                }
            ],
            left: false,
            currentPage: 0,
            pageSize: 12,
            totalPages: 0,
            totalElements: 0,
            selectedPrice: {
                price:    {
                    priceId: '',
                    tickerSymbol: '',
                    priceDate: '',
                    closePrice: 0,
                    openPrice: 0,
                    lowPrice: 0,
                    highPrice: 0,
                    volume: 0,
                    highLowRange: 0,
                    highLowVsClosePct: 0,
                    openCloseVsHighLowPct: 0,
                    closeVsLowDiffPct: 0,
                    closeVsHighDiffPct: 0,
                    dollarVolume: 0,
                    openCloseRange: 0
                },
                signalList: [],
                statisticList: [],
                avgPrices: [
                    {
                        avgList: []
                    }
                ],
                histSignals: {
                    fourWeek: [],
                    eightWeek: [],
                    twelveWeek: []
                }
            }
        }
    }

    retrieveTickerPage = (pageNum)=> {
        let url = buildUrl(TICKER_PAGE);
        let queryStr = 'page=' + pageNum + '&size=' + this.state.pageSize;
        axios.get(url + '?' + queryStr)
        .then(
            (res)=>{
                this.setState({
                    tickerList: res.data.content,
                    currentPage: res.data.number,
                    pageSize: res.data.size,
                    totalPages: res.data.totalPages,
                    totalElements: res.data.totalElements
                });
                this.retrieveCompositePrice(this.state.tickerList[0].tickerSymbol);
            },
            (res)=> {
                console.log(res.data);
            }
        );
    }

    nextPage = ()=> {
        let nextPg = this.state.currentPage + 1;
        this.retrieveTickerPage(nextPg);
    }

    prevPage = ()=> {
        let prevPage = this.state.currentPage -1;
        this.retrieveTickerPage(prevPage);
    }

    retrieveCompositePrice = (tickerSymbol)=> {
        axios.get(buildUrl(replacePathVar(PRICE_COMPOSITE, '{tickerSymbol}', tickerSymbol)))
        .then(
            (res)=> {
                this.setState({
                    selectedPrice: res.data
                });
            },
            (res)=> {
                console.log('retrieveCompositePrice', res.data);
            }
        );
    }

    toggleDrawer = (side, open) => () => {
        this.setState({
          [side]: open,
        });
      };
        componentDidMount() {
        this.retrieveTickerPage(0);
    }

    render() {
        return (
            <div className="content-grid">
                <div>
                    <Drawer open={this.state.left} onClose={this.toggleDrawer('left', false)}>
                        <div style={{margin:'5px',padding:'5px',border:'1px solid gray'}}>
                            <TextField
                                id='tickerSymbol'
                                label='Ticker Symbol'
                                margin='normal'
                                helperText='Type in ticker symbol and click Find'
                            />
                            <Button variant='contained'>Find</Button>
                        </div>
                    </Drawer>
                    <div className='sub-title'>Total stocks {this.state.totalElements}  Page {this.state.currentPage + 1}</div>
                    <div>
                        <Button variant='outlined' onClick={this.prevPage}>Previous</Button>
                        <Button variant='outlined' onClick={this.toggleDrawer('left', true)}>Search Ticker</Button>
                    </div>
                    <List>
                        <ListSubHeader>Stocks</ListSubHeader>
                        {this.state.tickerList.map(t=>
                        <ListItem button divider dense onClick={()=>this.retrieveCompositePrice(t.tickerSymbol)}>
                            <ListItemText primary={t.tickerSymbol} secondary={t.companyName}/>
                        </ListItem>)}
                    </List>
                    <div><Button variant='outlined' onClick={this.nextPage}>Next</Button></div>
                </div>
                <div>
                    <CompanyInfo priceInfo={this.state.selectedPrice}/>
                </div>
            </div>
        );
    }
}

export default StockList;



